<template>
  <page-layout :title="title">
    <template slot="action">
      <div class="interval-sovle">
        <a-button-group style="margin-right: 8px">
          <a-button
            icon="delete"
            @click="batchDelete"
            :loading="loading"
          >
            批量删除
          </a-button>
          <a-button
            icon="export"
            @click="showExportModal"
          >
            导出
          </a-button>
        </a-button-group>
        <a-button
          type="primary"
          icon="search"
          @click="showQueryModel"
        >
          <span>查询</span>
          <queryModal ref="dialogtreeQueryModal"></queryModal>
        </a-button>
      </div>
    </template>
    <div class="callHistory">
      <a-table
        :rowSelection="{selectedRowKeys: selectedRowKeys, onChange: onSelectChange}"
        :dataSource="dataSource"
        :columns="columns"
        @change="handleChange"
        :scroll="{ x: 1300 }"
        :pagination="pagination"
        :loading="loading"
      >
        <template
          slot="operation"
          slot-scope="text, record"
        >
          <a
            href="javascript:;"
            @click="onDelete(record.key)"
          >删除</a>
          <a-divider type="vertical" />
          <a
            href="javascript:;"
            @click="showRecordModal(record.key)"
          >录音</a>
          <recordModal ref="dialogtreeShowRecordModal"></recordModal>
        </template>
      </a-table>
    </div>
  </page-layout>
</template>

<script>
import ATable from "ant-design-vue/lib/table";
import ADivider from "ant-design-vue/lib/divider";
import AButton from "ant-design-vue/lib/button";
import { setTimeout, setImmediate } from "timers";
import recordModal from "./RecordModal.vue";
import queryModal from "./QueryModal";
import PageLayout from "@/components/Page/PageLayout";
const AButtonGroup = AButton.Group
export default {
  props: {
    customerId: String
  },
  components: {
    ATable,
    ADivider,
    AButton,
    AButtonGroup,
    recordModal,
    queryModal,
    PageLayout
  },
  data() {
    return {
      dataSource: [],
      selectedRowKeys: [],
      loading: false,
      loadingCallHistory: false,
      filteredInfo: null,
      sortedInfo: null,
      pagination: {
        showQuickJumper: true,
        pageSize: 10
      }
    };
  },
  created(){
    this.loading = true;
      this.loadingCallHistory = true;
      setTimeout(() => {
        this.getDataFromServer();
        this.loading = false;
        this.loadingCallHistory = false;
      }, 1000);
  },
  mounted() {},
  watch: {
    customerId: {
      handler: function(customerId) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.deleteLocalHistory();
        }, 1000);
      }
    }
  },
  computed: {
    title() {
      return this.$route.meta.title;
    },
    hasSelected() {
      return this.selectedRowKeys.length > 0;
    },
    columns() {
      let { sortedInfo, filteredInfo } = this;
      sortedInfo = sortedInfo || {};
      filteredInfo = filteredInfo || {};
      const columns = [
        {
          title: "方向",
          dataIndex: "direction",
          key: "direction",
          filters: [
            {
              text: "呼入",
              value: "呼入"
            },
            {
              text: "呼出",
              value: "呼出"
            }
          ],
          filteredValue: filteredInfo.direction || null,
          onFilter: (value, record) => record.direction.includes(value)
        },
        {
          title: "呼叫时间",
          dataIndex: "callTime",
          key: "callTime",
          sorter: (a, b) => a.callTime.length - b.callTime.length,
          sortOrder: sortedInfo.columnKey === "callTime" && sortedInfo.order
        },
        {
          title: "应答时间",
          dataIndex: "responseTime",
          key: "responseTime",
          sorter: (a, b) => a.responseTime.length - b.responseTime.length,
          sortOrder: sortedInfo.columnKey === "responseTime" && sortedInfo.order
        },
        {
          title: "挂机时间",
          dataIndex: "onHookTime",
          key: "onHookTime",
          sorter: (a, b) => a.onHookTime.length - b.onHookTime.length,
          sortOrder: sortedInfo.columnKey === "onHookTime" && sortedInfo.order
        },
        {
          title: "挂机原因",
          dataIndex: "onHookReason",
          key: "onHookReason",
          filters: [
            {
              text: "某原因",
              value: "某原因"
            },
            {
              text: "无原因",
              value: "无原因"
            }
          ],
          filteredValue: filteredInfo.direction || null,
          onFilter: (value, record) => record.direction.includes(value)
        },
        {
          title: "总时长",
          dataIndex: "totalTime",
          key: "totalTime",
          sorter: (a, b) => a.totalTime.length - b.totalTime.length,
          sortOrder: sortedInfo.columnKey === "totalTime" && sortedInfo.order
        },
        {
          title: "通话时长",
          dataIndex: "callDuration",
          key: "callDuration",
          sorter: (a, b) => a.callDuration.length - b.callDuration.length,
          sortOrder: sortedInfo.columnKey === "callDuration" && sortedInfo.order
        },
        {
          title: "操作",
          key: "operation",
          dataIndex: "operation",
          fixed: "right",
          width: 100,
          scopedSlots: {
            customRender: "operation"
          }
        }
      ];
      return columns;
    }
  },
  methods: {
    //      批量删除
    batchDelete() {
      let _this = this;
      this.$confirm({
        title: "你确定要删除选中的客户信息吗?",
        content: "Some descriptions",
        okText: "确定",
        okType: "danger",
        cancelText: "取消",
        onOk() {
          _this.loading = true;
          // ajax request after empty completing
          setTimeout(() => {
            _this.loading = false;
            _this.dataSource = _this.dataSource.filter(
              item => !_this.selectedRowKeys.includes(item.key)
            );
            _this.selectedRowKeys = [];
          }, 1000);
        },
        onCancel() {
        }
      });
    },
    showExportModal() {},
    //     批量修改
    showbatchModifyModal() {},
    onSelectChange(selectedRowKeys) {
      this.selectedRowKeys = selectedRowKeys;
    },
    getDataFromServer() {
      //TODO:通过AJAX请求获得呼叫历史信息（如果仅显示该坐席的与该客户的呼叫记录，那么参数参数为坐席id+客户id）
      for (let i = 0; i < 25; i++) {
        if ((i + 1) % 2 == 0) {
          this.dataSource.push({
            key: i,
            id: i,
            direction: `呼入`,
            callTime: `2018-08-${i} ${i}:00:00`,
            responseTime: `2018-08-${i} ${i}:00:00`,
            onHookTime: `2018-08-${i} ${i}:00:00`,
            onHookReason: "某原因",
            totalTime: `${i}:00:00`,
            callDuration: `${i}:00:00`
          });
        } else {
          this.dataSource.push({
            key: i,
            id: i,
            direction: `呼出`,
            callTime: `2018-08-${i} ${i}:00:00`,
            responseTime: `2018-08-${i} ${i}:00:00`,
            onHookTime: `2018-08-${i} ${i}:00:00`,
            onHookReason: "某原因",
            totalTime: `${i}:00:00`,
            callDuration: `${i}:00:00`
          });
        }
      }
    },
    deleteLocalHistory() {
      this.dataSource = [];
    },
    //点击才显示呼叫记录
    showCallHistory() {
      this.loading = true;
      this.loadingCallHistory = true;
      this.deleteLocalHistory();
      setTimeout(() => {
        this.getDataFromServer();
        this.loading = false;
        this.loadingCallHistory = false;
      }, 1000);
    },
    handleChange(pagination, filters, sorter) {
      this.filteredInfo = filters;
      this.sortedInfo = sorter;
    },
    onDelete(key) {
      let _this = this;
      this.$confirm({
        title: "你确定要删除该条记录吗?",
        content: "Some descriptions",
        okText: "确定",
        okType: "danger",
        cancelText: "取消",
        onOk() {
          _this.loading = true;
          // ajax request after empty completing
          setTimeout(() => {
            _this.loading = false;
            const dataSource = [..._this.dataSource];
            _this.dataSource = dataSource.filter(item => item.key !== key);
          }, 1000);
        },
        onCancel() {
        }
      });
    },
    showRecordModal(key) {
      this.$refs.dialogtreeShowRecordModal.showModal(key);
    },
    showQueryModel() {
      this.$refs.dialogtreeQueryModal.showQueryModal();
    }
  }
};
</script>

<style>
</style>



