<template>
  <div class="queryModal">
    <!--<Button type="primary" @click="showModal"> New Tag</Button>-->
    <a-modal title="查询" :visible="visible" :maskClosable="maskClosable" @ok="handleOk" :confirmLoading="confirmLoading" @cancel="handleCancel" width="907px">
      <query :structure="structure" ref="dialogtreeQuery"></query>
    </a-modal>
  </div>
</template>

<script>
  import AModal from 'ant-design-vue/lib/modal';
  import query from "@/components/Query/Query"
  
  // import { constants } from 'http2';
  /*
   * EditableCell Code https://github.com/vueComponent/ant-design-vue/blob/master/components/table/demo/EditableCell.vue
   */
  export default {
    components: {
      AModal,
      query
    },
    data() {
      return {
        maskClosable:false,//点击蒙层不关闭modal
        ModalText: "Content of the modal",
        visible: false,
        confirmLoading: false,
        //查询条件的结构
        structure: {
          fields: [
            {
              id: 1,
              field: "dir",
              text:"方向",
              type: "select",
              options: ["呼出", "呼入"],
              isDuration: false,
              isNumber: false //搜索的值是否为数值，如果为数值那么条件类型据此来变换
            },
            {
              id: 2,
              field: "agent_name",
              text:"坐席号码",
              type: "input",
              options: [],
              isDuration: false,
              isNumber: false //搜索的值是否为数值，如果为数值那么条件类型据此来变换
            },
            {
              id: 3,
              field: "other_number",
              text:"对方号码",
              type: "input",
              options: [],
              isDuration: false,
              isNumber: false //搜索的值是否为数值，如果为数值那么条件类型据此来变换
            },
            {
              id: 4,
              field: "created_datetime",
              text:"呼叫时间",
              type: "date",
              options: [],
              isDuration: false,
              isNumber: false //搜索的值是否为数值，如果为数值那么条件类型据此来变换
            },
            {
              id: 5,
              field: "hangup_cause",
              text:"挂机原因",
              type: "select",
              options: ["通话结束", "正常挂机", "被叫未注册", "被叫忙", "主叫取消", "无应答", "被叫无响应", "被叫拒绝", "无匹配路由", "未指定原因", "无效号码", "号码格式无效"],
              isDuration: false,
              isNumber: false //搜索的值是否为数值，如果为数值那么条件类型据此来变换
            },
            {
              id: 6,
              field: "talk_timed",
              text:"通话时长",
              type: "input",
              options: [],
              isDuration: true,
              isNumber: true //搜索的值是否为数值，如果为数值那么条件类型据此来变换
            },
            {
              id: 7,
              field: "queue",
              text:"队列",
              type: "input",
              options: [],
              isDuration: false,
              isNumber: false //搜索的值是否为数值，如果为数值那么条件类型据此来变换
            },
            
            
            // {
            //   id: 5,
            //   field: "关联用户",
            //   type: "input",
            //   options: ["离线", "在线", "在线一次接听", "阻塞"],
            //   isDuration: false,
            //   isNumber: true //搜索的值是否为数值，如果为数值那么条件类型据此来变换
            // },
            // {
            //   id: 6,
            //   field: "UUID",
            //   type: "input",
            //   options: [],
            //   isDuration: false,
            //   isNumber: false //搜索的值是否为数值，如果为数值那么条件类型据此来变换
            // },
            
            // {
            //   id: 8,
            //   field: "应答时间",
            //   type: "timePicker",
            //   options: [],
            //   isDuration: false,
            //   isNumber: false //搜索的值是否为数值，如果为数值那么条件类型据此来变换
            // },
            // {
            //   id: 9,
            //   field: "挂机时间",
            //   type: "timePicker",
            //   options: [],
            //   isDuration: false,
            //   isNumber: false //搜索的值是否为数值，如果为数值那么条件类型据此来变换
            // },
            
            // {
            //   id: 11,
            //   field: "总时长",
            //   type: "input",
            //   options: [],
            //   isDuration: true,
            //   isNumber: true //搜索的值是否为数值，如果为数值那么条件类型据此来变换
            // },
            
          ],
          stringConditions: [
          {
            id: 1,
            condition: "=",
            text:"等于",
            type: "input"
          },
          {
            id: 2,
            condition: "like_contain",
            text:"包含",
            type: "input"
          }
        ],
        numberConditions: [
          {
            id: 1,
            condition: "=",
            text:"等于",
            type: "input"
          },
          {
            id: 2,
            condition: "!=",
            text:"不等于",
            type: "input"
          },
          {
            id: 3,
            condition: ">",
            text:"大于",
            type: "input"
          },
          {
            id: 4,
            condition: ">=",
            text:"大于等于",
            type: "input"
          },
          {
            id: 5,
            condition: "<",
            text:"小于",
            type: "input"
          },
          {
            id: 6,
            condition: "<=",
            text:"小于等于",
            type: "input"
          }
        ],
        connectionWay: [
          {
            connection: "与"
          },
          {
            connection: "或"
          }
        ]
        },
      };
    },
    methods: {
      showQueryModal() {
        this.visible = true;
      },
      handleOk(e) {
        let  _this = this;
        this.$refs.dialogtreeQuery.dataSource.forEach(function(item) {
          if (item.fieldSelected) {
            _this.$refs.dialogtreeQuery.searchConditionResult = [
              ..._this.$refs.dialogtreeQuery.searchConditionResult,
              {
                key: item.key || {},
                field: item.fieldSelected || {},
                condition: item.conditionSelected || {},
                value: item.value || {}
              }
            ];
          }
        });
        this.ModalText = "The modal will be closed after two seconds";
        this.confirmLoading = true;
        setTimeout(() => {
          this.visible = false;
          this.confirmLoading = false;
          alert(JSON.stringify(this.$refs.dialogtreeQuery.searchConditionResult));
          this.$refs.dialogtreeQuery.searchConditionResult = [];
        }, 2000);
        //重置
      },
      handleCancel(e) {
        this.visible = false;
      },
    }
  };
</script>



